body {
  margin: 20px;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
}

input {
  width: 190px;
  margin: 10px 5px;
  padding: 5px;
  border: none;
  outline: none;
  background-color: #eaecef;
  border-radius: 3px;
  box-shadow: inset 0 0 7px #c1d4f2;
  transition: background-color 0.5s, box-shadow 0.5s;
}

input:focus {
  background-color: #dee2e8;
  box-shadow: inset 0 0 10px #c1d4f2;
}

button {
  padding: 5px 10px;
  margin: 5px;
  font-size: 17px;
  font-family: 'Titillium Web', sans-serif;
  background-color: #3f58fc;
  border: none;
  outline: none;
  border-radius: 5px;
  color: #fff;
  transition: background-color 0.5s;
  cursor: pointer;
}

button:active {
  box-shadow: inset 0 0 8px #000;
}

button:hover {
  background-color: #233ce0;
}


.Table {
    border-collapse: collapse;
    border: 2px solid #293bb3;
    width: 80%;
    margin: 0 auto;
    text-align: center;
}
  
.Table th, td {
    padding: 5px 10px;
    cursor: pointer;
}

tr:nth-child(2n) {
    background-color: #dee2e8;
}

tr:hover {
    box-shadow: inset 0 0 5px #afb0b3;
}

th {
    background-color: #293bb3;
    color: #ffffff;
}

.Ascending::after {
    content: '\25BC'
}

.Descending::after {
    content: '\25B2'
}
.DataLoader select,
.DataLoader button {
    margin: 0 5px;
}
.InfoBlock {
    width: 80%;
    margin: 10px auto;
    border: 2px solid #293bb3;
    padding: 5px 10px;
    box-sizing: border-box;
}

.InfoBlock textarea {
    width: 80%;
    height: 80px;
}

.InfoBlock div {
    margin: 5px 0;
}

.Filter select {
    margin: 0 5px;
}
.Pagination {
    text-align: center;
}
.Loader {
  font-size: 10px;
  margin: 50px auto;
  text-indent: -9999em;
  width: 11em;
  height: 11em;
  border-radius: 50%;
  background: #3f58fc;
  background: linear-gradient(to right, #3f58fc 10%, rgba(34,198,157, 0) 42%);
  position: relative;
  -webkit-animation: load3 1.4s infinite linear;
  animation: load3 1.4s infinite linear;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}
.Loader:before {
  width: 50%;
  height: 50%;
  background: #3f58fc;
  border-radius: 100% 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  content: '';
}
.Loader:after {
  background: #ffffff;
  width: 75%;
  height: 75%;
  border-radius: 50%;
  content: '';
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
@-webkit-keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

  
